.uk-overlay{box-shadow:0 1px 4px rgba(0,0,0,0.15);cursor:pointer;-webkit-transition:box-shadow 0.2s ease;transition:box-shadow 0.2s ease}.uk-overlay span{opacity:0;-webkit-transition:opacity 0.2s ease;transition:opacity 0.2s ease}.uk-overlay:not(.is-open):hover{box-shadow:0 5px 11px rgba(193,193,193,0.6)}.uk-overlay:not(.is-open):hover span{opacity:1}.uk-overlay-panel{background:rgba(255,255,255,0.8);padding:10px 10px 10px;color:#222}.uk-overlay-panel h3{font-weight:300}



